<template>
<v-container style="max-width: 576px">
    
        
            <v-card elevation=3>
                
                <v-card-text class="text-center">
                    <div>Mining <span class="primary--text">1 SVX</span> Burns <span class="red--text">{{miningCost}}
                            SOV</span>
                    </div>
                    <ul>
                        <li>Reward per action: <span class="primary--text">{{miningRate}}</span> SVX</li>
                        <li>Reward per transaction: <span class="primary--text">{{total.miningRate}}</span> SVX</li>
                        <li>Total Burn per ransaction: <span class="red--text">{{total.burn}}</span> SOV</li>
                        <li>Bonus for staking SVX: <span
                                :class="[miningBonus >= 50 ? 'green--text' : 'red--text']">{{miningBonus}}%</span></li>
                    </ul>
                </v-card-text>
            </v-card>
        

        
            <v-card elevation=3 class="mt-3" >
                <v-card-text>
                    <v-text-field type="number" label="Target reward" v-model="targetMiningRate" min="0" step="0.01" />
                    <div class="grey--text">Mine when reward is ≥ {{targetMiningRate}}</div>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-text>
                    <v-container class="d-flex justify-center">
                        <v-col>
                            <v-row>
                            <v-text>Auto</v-text>
                            </v-row>
                            <v-row>
                            <v-switch v-model="isAuto"></v-switch>
                            </v-row>
                        </v-col>
                        <v-col>
                            <v-btn large class="mt-4 black--text" color="warning" @click="submit" elevation=0><v-icon>local_fire_department</v-icon> Click mine x{{range}} </v-btn>
                        </v-col>
                    </v-container>

                    <v-slider v-model="range" min="1" max="200" step="1"></v-slider>
                    <div class="grey--text text-center">Bundled burn mining actions per transaction</div>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-text>
                    <bars :isAuto="isAuto" />
                </v-card-text>
            </v-card>
        
    
</v-container>
</template>

<script>
    import { mapState } from 'vuex'

    import bars from './bars'

    export default {
        data: () => ({
            targetMiningRate: 0,
            quantity: 84, // default value
            range: 1,
            isAuto: false,

            // statistic
            miningRate: 0,
            miningBonus: 0,

            pollingIsAuto: null,
            polling: null,
        }),
        computed: {
            ...mapState({
                eos: state => state.blockchain.eos,
                scatter: state => state.blockchain.scatter,
            }),
            total() {
                return {
                    miningRate: parseFloat((this.miningRate * this.range).toFixed(4)) || 0,
                    burn: parseFloat(((this.quantity * 0.025) * this.range).toFixed(2)) || 0,
                }
            },
            miningCost() {
                const cost = (this.total.burn / this.total.miningRate).toFixed(4)
                return cost == Infinity ? 0 : cost
            }
        },
        watch: {
            scatter(val) {
                if (val) this.getData()
            },
            isAuto(val) {
                if (val) {
                    this.submit() // init
                    this.pollingIsAuto = setInterval(() => this.submit(), 5000) // 5s
                    this.$notice.info('Auto mine <b>start</b>')

                    return false
                }

                if (this.pollingIsAuto) {
                    clearInterval(this.pollingIsAuto)
                    this.$notice.warning('Auto mine <b>stop</b>')
                }
            },
        },
        mounted() {
            this.polling = setInterval(() => this.getData(), 1000)
        },
        methods: {
            getData() {
                const svxMiningSupplyPromise = this.eos.getTableRows({
                    "json": "true",
                    "code": "svxmintofeos",
                    "scope": "svxmintofeos",
                    "table": "accounts"
                })

                const svxSupplyPromise = this.eos.getTableRows({
                    "json": "true",
                    "code": "svxmintofeos",
                    "scope": "SVX",
                    "table": "stat"
                })

                const svxPowerPromise = this.eos.getTableRows({
                    "json": "true",
                    "code": "svxmintofeos",
                    "scope": this.scatter.name,
                    "table": "accounts"
                })

                Promise.all([svxMiningSupplyPromise, svxSupplyPromise, svxPowerPromise])
                    .then(res => {
                        const svxMiningSupply = parseFloat(res[0].rows[0].balance)
                        const svxSupply = parseFloat(res[1].rows[0].supply)
                        const svxPower = parseFloat(res[2].rows[0].svxpower)

                        let bonusPercentage = (svxPower / svxSupply) * 10000
                        bonusPercentage = bonusPercentage > 50 ? 50 : Math.floor(parseFloat(bonusPercentage).toFixed(2))

                        let miningRate = (svxMiningSupply / 20000) * (1 + (bonusPercentage / 100)) * 1 // mining_multiplier

                        // set values
                        this.miningRate = parseFloat(miningRate.toFixed(4))
                        this.miningBonus = bonusPercentage
                    })
            },
            submit() {
                const myRange = this.range
                const action = {

                            account: 'sovmintofeos',
                            name: 'transfer',
                            authorization: [{
                                actor: this.scatter.name,
                                permission: "active"
                            }],
                            data: {
                                "from": this.scatter.name,
                                "to": 'sovdexrelays',
                                "quantity": '84.0000 SOV',
                                "memo": 'mine SVX'
                            }
                        }

                if ((this.miningRate < this.targetMiningRate)&&(!this.isAuto)) {
                    this.$notice.info('Reward is below target')
                }
                if (this.miningRate >= this.targetMiningRate) {
                    if ((this.miningRate >= this.targetMiningRate)&&(!this.isAuto)) {
                    this.$notice.info('Mine')
                }

                console.log("======================")
                console.log(action)

                var theactions = []
                for (var i =0; i < myRange; i++) {
                    theactions[i] = action
                }
                console.log(theactions)

                    this.eos.transaction({
                        actions: theactions
                        }).then(() => {
                        console.log('[mine] Success')
                        if (!this.isAuto) this.$notice.success('Mine success')

                        this.$bus.$emit('tick')
                    }).catch(error => console.error(error))
            }
        },
        beforeDestroy() {
            // clear if component destroy
            if (this.polling) clearInterval(this.polling)
        }},
        
        
        components: {
            bars
        }
    }
</script>

<style lang="scss" scoped>
    ul {
        margin: 0;

        li {
            margin-top: 0;
        }
    }
</style>